$background: #b2e59b;
$font1: #1a1a1a;
$font2: #414141;
$bigText: 30px;
$mediumText: 25px;
$normalText: 22px;
$smallText: 18px;

$bp-xlg: 1900px;
$bp-lg: 1600px;
$bp-md: 1200px;
$bp-sm: 820px;
$bp-xsm: 600px;
$bp-phone: 400px;
$nav-align: row;
:root {
  --full-height: 100%;
  --nav-bg-font: 55px;
  --project-card-title: 22px;
  --nav-sm-font: 40px;
  --modal-pad: 30px;
  --modal-width: 700px;
  --about-width: 1000px;
  --nav-filter-h: 160px;
  --nav-h: 100px;
  --shift-project: 200px;
  --shift-layers: 0px;
}

* {
  margin: 0;
  padding: 0;
}

@mixin fullHeight {
  height: 100vh;
  @media (max-height: $bp-xsm) {
    height: -webkit-fill-available;
    height: -mozkit-fill-available;
    height: fill-available;

  }

}

html, body, #root, .App {
  @include fullHeight;
}


@font-face {
  font-family: "custom_regular";
  src: local("custom_regular"), url(./fonts/custom_regular.otf) format('opentype');
}

@font-face {
  font-family: "custom_bolder";
  src: local("custom_bolder"), url(./fonts/custom_bolder.otf) format('opentype');
}

body {
  color: #2f2f2f;
}


.App {
  font-family: "custom_regular", sans-serif;
  z-index: -1;
  overflow: hidden;
  width: 100vw;
  background: linear-gradient(290deg, rgba(141, 104, 179, 0.41) 0%, rgba(141, 104, 179, 0.12) 100%),
  linear-gradient(70deg, rgba(248, 128, 128, 0.31) 0%, transparent 50%, rgba(127, 158, 204, 0.25) 100%),
  #FFFFFF;
  //filter: invert(1);
}


.navContainer {
  @include fullHeight;
  width: 100%;
  display: flex;
  flex-direction: $nav-align;
  align-items: stretch;
  transition: 0.6s;

  .navElement {
    font-family: "custom_regular", sans-serif;
    transition: 0.6s;
    background: rgba(65, 65, 65, 0.2);
    display: flex;
    align-items: center;
    justify-content: center;
    color: #f1f1f1;
    font-size: var(--nav-bg-font);
    width: (100%/3);

    &.clickable {
      cursor: pointer;

      &:hover {
        box-shadow: 0 0 100px 20px #ffffffa1;
        z-index: 1;
        background: rgba(65, 65, 65, 0.2);
      }
    }
  }

  &.grow {
    @include fullHeight;
  }

  &.shrink {
    position: sticky;
    top: 0;
    height: var(--nav-h);

    .navElement {
      height: 100%;
      transition: 0.4s;
      background: transparent;
      color: #3e3e3e;
      font-size: var(--nav-sm-font);
      cursor: pointer;

      &:hover {
        box-shadow: none;

        background: rgba(152, 152, 152, 0.29);
      }
    }
  }

}

.projectsContainer {
  height: calc(100% - var(--nav-filter-h) - 60px);

  padding-bottom: 40px;
  //border-top: rgba(0, 0, 0, 0.38) solid 2px;
  transition: 0.6s;
  position: fixed;
  top: 100vh;


  bottom: 0;
  padding-top: 10px;
  //left: 5%;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, 250px);
  grid-template-rows:  repeat(auto-fill, 240px);
  gap: 50px;
  max-width: 1200px;

  justify-content: center;
  overflow-y: scroll;
  overflow-x: hidden;

  &.mounted {
    top: var(--nav-filter-h);
  }

  &.shiftDown {
    top: var(--shift-project);
  }

  .projectSmall {
    cursor: pointer;
    border-radius: 20px 20px 10px 10px;
    background-color: rgba(255, 255, 255, 0.75);
    display: flex;
    flex-direction: column;
    overflow: hidden;
    transition: 0.5s;
    height: 235px;

    .previewImage {
      transition: 0.5s;
      //border-radius: 80px 80px 0px 0px;
      height: 175px;
      object-fit: cover;
    }

    .projectTitle {
      font-size: var(--project-card-title);
      font-family: "custom_bolder", sans-serif;
      font-weight: normal;

      margin-top: -15px;
      padding: 35px 5px 15px 5px;
      transition: 0.5s;
    }

    .projectDescription {
      padding: 5px;
      transition: 0.5s;
      min-height: 30px;
    }

    &:hover {
      border-radius: 10px;

      .previewImage, .projectTitle, .projectDescription {
        transform: translate(0, -170%);
        //border-radius: revert;
      }
    }
  }

}

.tagsContainer {
  //overflow: hidden;
  font-size: 18px;
  transition: 0.5s;
  position: relative;
  top: 10px;
  max-width: 1200px;
  right: 10px;
  float: right;
  width: 150px;
  background-color: rgba(255, 255, 255, 0.38);
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  cursor: pointer;

  &:hover:not(.active) {
    background-color: rgba(255, 255, 255, 0.92);
  }
  .tag, .close {
    overflow: hidden;
    width: 0;
    transition: 0.3s;
    opacity: 0;
  }

  &.active {
    right: revert;
    margin: 10px;
    float: revert;
    cursor: revert;
    width: auto;
    flex-wrap: wrap;
    height: revert;
    justify-content: stretch;
    background-color: transparent;

    .tag, .close {
      background-color: rgba(255, 255, 255, 0.75);
      margin: 3px;
      padding: 7px;
      border-radius: 4px;
      opacity: 1;
      cursor: pointer;
      width: revert;

      &.clicked {
        background-color: rgba(157, 156, 156, 0.84);

      }
    }

    .close {
      background-color: #F880804F;

      &:hover {
        background-color: rgba(174, 94, 94, 0.7);

      }
    }
  }
}

@mixin buttonContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

@mixin buttons {
  transition: 400ms;
  border-radius: 2px;
  margin: 5px;
  padding: 10px;
  white-space: nowrap;
  text-decoration: none;
  &:hover {
    //background: rgba(222, 222, 222, 0.94);
    box-shadow: 0 0px 4px black inset;
  }
}

.modalContainer {
  overflow-x: scroll;
  transition: 0.6s;
  position: fixed;
  top: 0;
  height: var(--full-height);
  width: 100vw;
  cursor: w-resize;
  backdrop-filter: blur(0px);
  overflow-x: hidden;

  &.mounted {
    backdrop-filter: blur(6px);

    .modalProject {
      margin-top: 100px
    }
  }

  .modalProject {

    border-radius: 5px;
    transition: 0.5s;
    cursor: default;
    padding: var(--modal-pad);
    width: calc(var(--modal-width) - (2 * var(--modal-pad)));
    margin: auto;
    margin-top: 120vh;
    background-color: rgba(255, 255, 255, 0.91);

    .modalCover {
      width: 100%;
    }

    .modalName {
      text-align: center;
      margin: 50px;
    }

    .modalDesc {
      font-size: 20px;

      .descrParagraph {
        line-height: 24px;
        margin-bottom: 15px;
      }
    }

    .smallTitle {
      font-family: "custom_bolder", sans-serif;
      padding: 0;
      margin: 20px;
      margin-left: 0px;
      font-weight: normal;

    }

    .languages {
      white-space: nowrap;
      display: inline-block;
    }

    .languages:not(:last-child) {

      padding-right: 30px;
    }

    .linkContainer {
      @include buttonContainer;
      margin: 10px;

      .gitLink, .deployLink {
        @include buttons;

        &.deployLink {
          border: solid 2px #6976c5;
          color: #6976c5;
        }

        &.gitLink {
          color: #393737;
          border: solid 2px #393737;
        }
      }
    }
  }

}

.aboutContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: scroll;
  width: 100vw;
  transition: 0.6s;

  position: fixed;
  top: 100vh;

  &.mounted {
    height: calc(100% - var(--nav-h));
    top: var(--nav-h);
  }

  .aboutPage {
    padding: 30px 0 30px 0;

    border-radius: 5px;
    background-color: rgba(255, 255, 255, 0.61);

    width: 100vw;
    max-width: var(--about-width);

    .introduction {

      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      align-items: center;
      justify-content: space-evenly;
      gap: 20px;

      .textContainer {
        display: inline-block;
        padding: 20px;
        width: 600px;

        .text {
          font-size: 22px;
          line-height: 30px;
          vertical-align: center;
          display: inline-block;

          &:first-child {
            padding-bottom: 5px;
          }
        }
      }
      .profilePict {
        width: 150px;
      }
      .buttons {
        @include buttonContainer;
        .link {
          @include buttons;
          &.linkedIn {
            border: solid 2px #6976c5;
            color: #6976c5;
          }

          &.github {
            color: #393737;
            border: solid 2px #393737;
          }
        }
      }
    }
  }
}


@media (max-width: $bp-xsm) {
  :root {
    --project-card-title: 20px;
    --nav-bg-font: 40px;
    --nav-sm-font: 24px;
    --modal-width: 100%;
    --modal-pad: 15px;
    --nav-filter-h: 100px;
    --shift-project: 250px;
    --nav-h: 50px;
  }

  .aboutContainer > .aboutPage {
    margin-top: 45px;
    .introduction > .textContainer {
      width: 100%;

    }

  }

  .navContainer {
    flex-direction: column;
    justify-content: space-between;

    .navElement {
      width: 100%;
      flex: 1;
    }

    &.shrink {
      height: var(--nav-h);
      background: rgba(132, 105, 244, 0.45);

      .navElement {
        height: 100%;

        &.clickable {
          display: none;
        }
      }
    }
  }

  .projectsContainer {
    grid-template-columns: repeat(auto-fit, 250px);
    grid-template-rows:  repeat(auto-fill, 240px);
    gap: 10px;

    &.shiftDown {

    }

    .projectSmall {
      height: 230px;

      .previewImage {
        transition: 0.5s;
      }
    }

  }

  .tagsContainer {
    top: 5px;
    width: 150px;
    background-color: rgba(255, 255, 255, 0.53);
    height: 30px;
    .tagTitle {
      font-size: 20px;
    }

  }
  .modalContainer {

    .modalProject {

      .modalName {
        text-align: center;
        margin: 20px 0 20px 0;
      }

      .modalDesc {
        font-size: 18px;

      }


    }


  }
}

@media (max-width: $bp-phone) {

  :root {
    --shift-project: 450px;
    --full-height: -webkit-fill-available;
  }

  .aboutContainer {
    .aboutPage {
      margin-top: 400px;
      margin-bottom: 60px;
      .introduction>.textContainer>.text{
        font-size: 16px;
      }

    }


  }

}

@media (min-width: $bp-xsm) and (max-width: $bp-sm) {
  :root {
    --nav-bg-font: 32px;
    --nav-sm-font: 28px;
    --modal-width: 100%;
    --modal-pad: 15px;
    --shift-project: 250px;
  }
  @media (min-width: 700px) {
    :root {
      --modal-pad: 30px;
      --modal-width: 700px;
    }
  }
}

@media(min-width: $bp-sm) and (max-width: $bp-md) {
  :root {
    --nav-sm-font: 32px;
    --nav-bg-font: 42px;
    --shift-project: 210px;
  }
}

@media(min-width: $bp-md) {
  :root {

  }
  $m-projects-p: calc((100vw - 1200px) / 2);
  .projectsContainer {
    left: $m-projects-p;

  }
  .tagsContainer {
    right: $m-projects-p;

    &.active {
      left: $m-projects-p;
    }
  }
}

